.stars {
  display: flex;
  align-items: center;

  &__star {
    height: 16px;
    width: 16px;
    background: no-repeat url(/src/images/star.svg) center;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  &--1 {
    & .stars__star:nth-child(1) {
      background-image: url(/src/images/star-active.svg);
    }
  }

  &--2 {
    & .stars__star:nth-child(-n + 2) {
      background-image: url(/src/images/star-active.svg);
    }
  }

  &--3 {
    & .stars__star:nth-child(-n + 3) {
      background-image: url(/src/images/star-active.svg);
    }
  }

  &--4 {
    & .stars__star:nth-child(-n + 4) {
      background-image: url(/src/images/star-active.svg);
    }
  }

  &--5 {
    & .stars__star:nth-child(-n + 5) {
      background-image: url(/src/images/star-active.svg);
    }
  }
}
