body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  align-items: center;
  column-gap: 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link--active {
  color: #00acdc;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__sku {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.product-card__pricing {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__pricing-label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__pricing-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.content {
  grid-template-rows: 60px auto;
  grid-template-columns: 1fr;
  column-gap: 50px;
  display: grid;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
